.sidebar {
    grid-column: 4 span;
    margin-left: 100px;

    &__title {
        font-size: 16px;
        line-height: 21px;
        border: 1px solid #E5E5E5;
        padding: 15px 0 14px;
        text-align: center;
        color: $black;
        font-weight: 400;
        margin-bottom: 20px;

        &--categories {
            margin-bottom: 7px;
        }

        &--tags {
            margin-bottom: 10px;
        }
    }

    &__block:not(:first-child) {
        margin-top: 70px;
    }
}

.about-author {
    &__wrapper {
        filter: drop-shadow(0px 2px 6px rgba(0, 0, 0, 0.1));
        overflow: hidden;
        width: 100%;
        height: 100%;
        background-color: $white;
    }

    &__img {
        background-color: $gray;
        width: 100%;
        height: 180px;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            display: block;
        }
    }

    &__content {
        padding: 20px 20px 15px;
        text-align: center;
    }

    &__name {
        font-weight: 700;
        font-size: 16px;
        line-height: 21px;
        color: $black;
    }

    &__position {
        margin-top: 4px;
        font-size: 14px;
        line-height: 19px;
    }

    &__text {
        margin-top: 14px;
        font-size: 14px;
        line-height: 24px;
        color: #666;
        font-family: 'PT Sans', sans-serif;

        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
    }

    &__more {
        margin-top: 14px;
        border-top: 1px solid #E5E5E5;
        padding: 12px 0;
        font-size: 12px;
        line-height: 16px;
        color: $laser;
        text-transform: uppercase;
        width: 100%;
        text-align: center;
    }
}

.featured-posts {
    &__item {
        &:not(:last-child) {
            margin-bottom: 30px;
        }
    }

    &__wrapper {
        display: flex;
        flex-direction: column;
        overflow: hidden;

        filter: drop-shadow(0px 2px 6px rgba(0, 0, 0, 0.1));
        background-color: $white;
    }

    &__img {
        background-color: $gray;
        width: 100%;
        height: 160px;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            display: block;
        }
    }

    &__content {
        padding: 15px 10px;
        text-align: center;
        height: 100%;
        display: flex;
        flex-direction: column;
    }

    &__name {
        text-transform: uppercase;
        font-size: 12px;
        line-height: 16px;
        color: $laser;
    }

    &__title {
        font-weight: 400;
        margin-top: 4px;
        font-size: 18px;
        line-height: 26px;
        color: $black;
    }

    &__info {
        padding-top: 10px;
        margin-top: auto;
        display: flex;
        align-items: center;
        justify-content: center;

        font-size: 12px;
        line-height: 16px;
    }

    &__date {
        color: #666;
        margin-right: 10px;

    }

    &__author {
        color: #666;

        span {
            color: $gray;
        }
    }
}



.categories {
    &__item {
        &:not(:first-child) {
            .categories__title {
                border-top: 1px solid #E5E5E5;
            }
        }
    }

    &__title {
        width: 100%;
        text-align: left;
        border: none;
        background-color: transparent;
        color: #fff;
        padding: 10px;
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 14px;
        line-height: 19px;
        color: #666666;
    }

    &__list._init &__title {
        cursor: pointer;
        padding: 14px 0;
    }

    &__content {
        color: $gray;
        padding: 0 10px 10px;
        font-size: 14px;
        line-height: 19px;
    }
}

.social-media {
    &__list {
        display: grid;
        grid-template-columns: repeat(3, 89px);
        grid-template-rows: repeat(3, 89px);
    }

    &__link {
        width: 100%;
        height: 100%;
        padding-top: 17px;
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        transition: background-color 0.3s ease-in-out;

        svg {
            width: 20px;
            height: 20px;
            fill: #5A5A5A;
            transition: fill 0.3s ease-in-out;
        }

        &--fb {
            svg {
                width: 11px;
                height: 20px;
            }
        }

        &--pinterest {
            svg {
                width: 15px;
                height: 20px;
            }
        }

        &--vimeo {
            svg {
                width: 20px;
                height: 18px;
            }
        }

        &--dribbble {
            svg {
                width: 20px;
                height: 20px;
            }
        }

        &--twitter {
            svg {
                width: 20px;
                height: 17px;
            }
        }

        &--behance {
            svg {
                width: 20px;
                height: 13px;
            }
        }

        &--inst {
            svg {
                width: 20px;
                height: 20px;
            }
        }

        &--youtube {
            svg {
                width: 16px;
                height: 20px;
            }
        }

        &--google {
            svg {
                width: 22px;
                height: 14px;
            }
        }

        &:hover {
            background-color: $laser;

            svg {
                fill: $white;
            }

            .social-media__stats {
                color: $white;
            }
        }
    }

    &__stats {
        margin-top: 6px;
        font-size: 12px;
        line-height: 16px;
        color: #999;
        font-family: 'PT Sans', sans-serif;
        transition: color 0.3s ease-in-out;

        span {
            display: block;
        }
    }
}

.tags {
    &__list {
        display: flex;
        flex-wrap: wrap;
    }

    &__item {}

    &__link {
        padding: 6px 17px 5px;
        font-size: 14px;
        line-height: 19px;
        color: #666;
        transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;

        &:hover {
            background-color: $laser;
            color: $white;
        }
    }
}


.pagination {
    margin-bottom: 50px;
    border: 1px solid #E5E5E5;
    margin-top: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Space Grotesk', sans-serif;

    &__list {
        display: flex;
    }

    &__item {

        &:not(:last-child) {
            margin-right: 10px;
        }
    }

    &__link {
        transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
        padding: 18px 22px 16px;
        font-size: 16px;
        line-height: 16px;
        color: $black;

        &:hover {
            background-color: $laser;
            color: $white;
        }

        &--ellipsis {
            pointer-events: none;

            &:hover {
                background-color: transparent;
            }
        }
        &--active{
            border-bottom: 2px solid $laser;
        }
    }

    &__nav {
        font-size: 14px;
        line-height: 16px;
        color: #A2A2A2;
        text-transform: uppercase;
        position: relative;
        transition: color 0.3s ease-in-out;
        &::before {
            content: '';
            display: block;
            width: 6px;
            height: 6px;
            position: absolute;
            top: 5px;
            border: 1px solid currentColor;
            mix-blend-mode: normal;
            border-bottom: 0px;
            border-left: 0px;
        }



        &--older {
            margin-right: 30px;
            padding-left: calc(6px + 10px);

            &::before {
                left: 0;
                transform: rotate(225deg);
            }
        }

        &--next {
            margin-left: 30px;
            padding-right: 14px;

            &::before {
                right: 0;
                transform: rotate(45deg);
            }
        }
        &:hover{
          color: $black;
        }
    }
}
