.header{
    position: fixed;
    left: 0;
    top: 0;
    z-index: 600;
    width: 100%;
    border-bottom: 1px solid #bfbfbf;
    background-color: $white;
	&__container{
        padding-top: 25px;
        padding-bottom: 25px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}
.burger{
    display: none;
}