.nav {
    &__list {
        display: flex;
    }

    &__item {
        &:not(:last-child) {
            margin-right: 35px;
        }
    }

    &__link {
        color: $black;
        font-size: 14px;
        line-height: 19px;

        &--active {
            color: $laser;
        }
    }
}
