.social {
    display: flex;

    &__item {
        width: 34px;
        height: 34px;
        background-color: $white;
        border: 1px solid #E5E5E5;
        border-radius: 50%;
        transition: background-color 0.3s ease-in-out;

        &:not(:last-child) {
            margin-right: 20px;
        }

        &:hover {
            background-color: $laser;

            svg {
                fill: $white;
            }
        }
    }

    &__link {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;

        svg {
            transition: fill 0.3s ease-in-out;
            width: 15px;
            height: 15px;
            fill: #666;
        }

        &--fb {
            svg {
                width: 9px;
                height: 16px;
            }
        }

        &--twitter {
            svg {
                width: 16px;
                height: 13px;
            }
        }

        &--pinterest {
            svg {
                width: 13px;
                height: 16px;
            }
        }

        &--google {
            svg {
                width: 20px;
                height: 13px;
            }
        }

        &--behance {
            svg {
                width: 18px;
                height: 11px;
            }
        }

        &--inst {
            svg {
                width: 17px;
                height: 17px;
            }
        }
    }
}
