.posts {
    grid-column: 8 span;

    &__list {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 30px;
    }
}

.posts-item {
    &--big {
        margin-top: 30px;
        grid-column: 2 span;

        .posts-item__img {
            max-height: 350px;
        }

        .posts-item__content {
            text-align: left;
            padding: 25px 126px 25px 30px;
        }

        .posts-item__info {
            justify-content: start;
        }

        .posts-item__comments {
            margin-left: 10px;
            font-size: 12px;
            line-height: 16px;
            color: #666666;
        }

        .posts-item__text {
            margin-top: 15px;
            font-size: 14px;
            line-height: 24px;
            color: #666666;
            font-family: 'PT Sans', sans-serif;
        }
    }

    &__wrapper {
        display: flex;
        flex-direction: column;
        overflow: hidden;
        height: 100%;
        filter: drop-shadow(0px 2px 6px rgba(0, 0, 0, 0.1));
        background-color: $white;
    }

    &__img {
        background-color: blue;
        width: 100%;
        max-height: 280px;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            display: block;
        }
    }

    &__content {
        padding: 15px 40px;
        text-align: center;
        height: 100%;

        display: flex;
        flex-direction: column;
    }

    &__name {
        text-transform: uppercase;
        font-size: 12px;
        line-height: 16px;
        color: $laser;
    }

    &__title {
        font-weight: 400;
        margin-top: 4px;
        font-size: 20px;
        line-height: 28px;
        color: $black;
    }

    &__info {
        padding-top: 10px;
        margin-top: auto;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__date {
        font-size: 12px;
        line-height: 16px;
        color: #666;
        margin-right: 10px;
    }

    &__author {
        font-size: 12px;
        line-height: 16px;
        color: #666;

        span {
            color: $gray;
        }
    }
}
