.hero {
    margin-top: 85px;
    color: $white;
    background-repeat: no-repeat;
    background-size: cover;
    max-height: 700px;
    height: 100vh;

    &__container {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        height: 100%;
        padding-bottom: 60px;
    }

    &__content {
        max-width: 560px;
    }

    &__subtitle {
        font-size: 14px;
        line-height: 18px;
        text-transform: uppercase;
        margin-bottom: 4px;
        font-family: 'PT Sans', sans-serif;
    }

    &__title {
        font-weight: 700;
        font-size: 40px;
        line-height: 50px;
    }

    &__info {
        font-size: 14px;
        line-height: 19px;
        margin-top: 10px;
        display: flex;
    }

    &__time {
        margin-right: 10px;
    }

    &__author {
        margin-right: 10px;

        span {
            color: $gray;
        }
    }

    &__comments {}

}
