.footer {
    margin-top: 100px;
    padding-bottom: 30px;

    &__container {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    &__nav {
        margin-top: 60px;
        font-family: 'PT Sans', sans-serif;

        .nav__list {
           flex-wrap: wrap;
        }
        .nav__item{
            margin-bottom: 10px;
        }
        .nav__link {
            transition: color 0.3s ease-in-out;
            font-size: 14px;
            line-height: 18px;
            color: #666666;

            &:hover {
                color: $laser;
            }
        }
    }

    &__social {
        margin-top: 20px;
    }

    &__copyright {
        margin-top: 70px;
        font-size: 14px;
        line-height: 18px;
        font-family: 'PT Sans', sans-serif;
        color: #666;
    }
}
