@media (max-width: 1175px) {
    .hero {
        background-position: center;
    }
}

@media (max-width: 1024px) {
    .instagram__list {
        gap: 5px;
    }

    .sidebar__block:not(:first-child) {
        margin-top: 0px;
    }

    .posts-sidebar__container {
        display: block;
    }

    .sidebar {
        margin: 0;
        margin-top: 100px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
    }

    .sidebar__author {
        order: 1;
        max-width: 270px;
    }

    .about-author__wrapper {
        height: auto;
    }

    .sidebar__featured-posts {
        order: 4;
        margin-top: 50px !important;
    }

    .sidebar__categories {
        order: 3;
        margin-top: 50px !important;
        flex-basis: 100%;
    }

    .sidebar__social {
        order: 2;
        max-width: 270px;
    }

    .sidebar__tags {
        order: 5;
        flex-basis: 100%;
        margin-top: 50px !important;
    }

    .featured-posts__items {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 30px;
    }

    .posts-item__content {
        padding: 15px;
    }
}

@media (max-width: 700px) {
    .instagram__list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
    }

    .instagram__item {}

    .sidebar__author {
        max-width: 100%;
    }

    .sidebar__social {
        flex-basis: 100%;
        max-width: 100%;
        margin-top: 50px !important;
    }

    .social-media__list {
        justify-content: center;
    }

    .pagination__nav {
        font-size: 0;
        height: 100%;

        &::before {
            top: 2px;
            width: 12px;
            height: 12px;
        }
    }
}

@media (max-width: 600px) {
    .footer__nav{
        .nav__list{
            justify-content: center;
        }
    }
    .tags__list {
      justify-content: space-between;
    }
    .featured-posts__content {
        padding: 10px 7px;
    }

    .featured-posts__title {
        font-size: 15px;
        line-height: 20px;
    }

    .header__nav {
        display: flex;
    }

    .posts__item {
        grid-column: 2 span;
    }

    .posts-item--big .posts-item__content {
        text-align: left;
        padding: 15px 25px 15px;
    }

    .posts-item__info {
        justify-content: space-between;
    }

    .header__nav-list {
        position: fixed;
        right: 0;
        top: 85px;
        z-index: 10000;
        background-color: #fff;
        margin-top: 0;
        height: 100vh;
        padding-bottom: 25px;
        width: 50vw;
        min-width: 180px;
        overflow-y: auto;
        transform: translateX(100vw);
        transition: transform .3s ease-in-out;

        flex-direction: column;

        .nav__item {
            margin: 0;

        }

        .nav__link {
            border-bottom: 1px solid #edecf2;
            width: 100%;
            padding: 10px 10px;
            transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;

            &:hover {
                background-color: $laser;
                color: $white;
            }
        }
    }

    .header__nav-list.header__nav-list--active {
        transform: translateX(0);

    }

    .burger {
        --burger-width: 30px;
        --burger-height: 17px;
        --burger-line-height: 2px;
        display: block;
        @include burger;
    }
}

@media (max-width: 500px) {
    .instagram__list {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
    .pagination {
        justify-content: space-between;
    }

    .pagination__item:not(:last-child) {
        margin-right: 0px;
    }

    .pagination__link {
        padding: 13px 16px 11px;
        font-size: 14px;
        line-height: 14px;
    }

    .pagination__nav {
        margin: 0;

        &::before {
            top: 2px;
            width: 10px;
            height: 10px;
        }

        &--next {
            margin-right: 15px;
        }

        &--older {
            margin-left: 15px;
        }
    }

    .hero__title {
        font-size: 25px;
        line-height: 35px;
    }

    .hero__info {
        font-size: 12px;
        line-height: 15px;
        justify-content: space-between;
    }

}
