.instagram {
    margin-top: 80px;
    &__title {
        font-size: 24px;
        line-height: 32px;
        color: $black;
        font-weight: 400;
        text-align: center;
        display: block;
    }

    &__list {
        margin-top: 40px;
display: flex;
gap: 21px;

    }

    &__item {
        width: 100%;
        height: 100%;
    }

    &__link {
        display: block;
        width: 100%;
        height: 100%;
        background-color: $gray;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            display: block;
        }
    }
}
